<template>
  <div class="overtune">
    <h1>Camera del Cavallo</h1>
    <ul class="gallery-list">
      <li v-for="project in gallery" :key="project.codeName">
        <router-link :to="project.codeName">{{project.name}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Overtune",
  computed: {
    gallery() {
      return this.$store.state.gallery;
    }
  }
};
</script>



